.delete-btn {
  float: right;
  color: #e74944;
  margin-left: 42px;
  font-size: 12pt;
  border: 1px solid #e74944;
  border-radius: 3px;
  padding: 3px 5px 3px 5px;
}

.delete-btn:hover {
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.24), 0 5px 7px 0 rgba(0, 0, 0, 0.19);
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.media-snippet {
  padding: 1.5rem;
}

.media {
  align-items: flex-start;
  display: flex;
  text-align: left;
}

figure {
  display: block;
  margin: 0;
  padding: 0;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 40px;
          margin-inline-start: 40px;
  -webkit-margin-end: 40px;
          margin-inline-end: 40px;
}
.media-left {
  margin-right: 1rem;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
  margin-left: 0px;
  margin-top: 0px;
}

.media-content {
  flex-basis: auto;
  flex-grow: 1;
  flex-shrink: 1;
  text-align: left;
}

.media .content:not(:last-child) {
  margin-bottom: 0.75rem;
}

.media-right {
  margin-left: 1rem;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
}

.image {
  display: block;
  position: relative;
}
p {
  display: block;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}

.level.is-mobile {
  display: flex;
}
.level-left {
  align-items: center;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
}
.level-item {
  align-items: center;
  display: flex;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
}

.view-btn {
  border: 1px solid darkcyan;
  border-radius: 3px;
  padding: 3px 5px 3px 5px;
  width: 30%;
  font-size: 16pt;
}
.view-btn:hover {
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.24), 0 5px 7px 0 rgba(0, 0, 0, 0.19);
}

.search-btn {
  align-items: center;
  margin-left: 8px;
}

.save-btn {
  color: darkcyan;
  align-content: center;
  font-size: 16pt;
  margin-left: 50%;
  border: 1px solid darkcyan;
  border-radius: 3px;
  padding: 3px 5px 3px 5px;
  width: 20%;
}

.save-btn:hover {
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.24), 0 5px 7px 0 rgba(0, 0, 0, 0.19);
}

/* Styles for Nav */

 /* unvisited link */
  a:link {
    color: blue;
  }
  
  /* visited link */
  a:visited {
    color: darkblue;
  }
  
  /* mouse over link */
  a:hover {
    color: hotpink;
  }
  
  /* selected link */
  a:active {
    color: blue;
  }
